
import { Indicator } from 'mint-ui'
import { Toast } from 'mint-ui';
export default {
  openIndicator () {
    Indicator.open({
      text: '...',
      spinnerType: 'snake'
    })
  },
  closeIndicator () {
    Indicator.close()
  },
  register () {

    let instance = Toast({
      message: '',
      position: 'bottom'
    })

    setTimeout(() => {
      instance.close();
    }, 2000);
  },
  following () {

    let instance = Toast({
      message: '',
      position: 'bottom'
    })

    setTimeout(() => {
      instance.close();
    }, 2000);
  },
  pop () {
    let instance = Toast('(_`)')
    setTimeout(() => {
      instance.close();
    }, 2000);
  },
  cmtPop () {
    let instance = Toast('(_`)')
    setTimeout(() => {
      instance.close();
    }, 2000);
  }
}



